/* src/styles/footer.css */

/* Footer Styling */
.hotel-footer {
  background: linear-gradient(45deg, #1c1c1c, #333333, #444444);
  background-size: 300% 300%;
  animation: gradientAnimation 10s ease infinite;
  color: white;
  padding: 40px 20px;
  font-family: 'Roboto', sans-serif;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.3);
  text-align: center;
  position: relative;
  overflow: hidden;
  transform: translateY(0px);
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.footer-content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  animation: fadeInUp 1.5s ease-out;
}

.footer-left, .footer-middle, .footer-right {
  flex: 1;
  padding: 20px;
  min-width: 250px;
}

.footer-left h4, .footer-middle h5, .footer-right h5 {
  margin-bottom: 20px;
  font-size: 1.8em;
  font-weight: 700;
  color: #f39c12; /* Gold color for headers */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* Text shadow */
}

ul {
  list-style-type: none;
  padding: 0;
}

ul li a {
  color: white;
  text-decoration: none;
  display: block;
  margin-bottom: 10px;
  font-size: 1.2em;
  transition: color 0.3s ease, transform 0.3s ease;
}

ul li a:hover {
  color: #f39c12;
  transform: translateX(10px); /* Slide effect on hover */
  text-decoration: underline;
}

/* Social Media Icons Styling */
.social-icons {
  display: flex;
  gap: 30px;
  margin-top: 20px;
  margin-left: 38px;
}

.social-icons a {
  color: white;
  font-size: 45px;
  text-decoration: none;
  transition: transform 0.3s ease, color 0.3s ease, opacity 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.social-icons a:hover {
  color: #f39c12;
  transform: translateY(-8px); /* Bounce up effect */
  opacity: 0.8;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3); /* Hover shadow effect */
  animation: pulseIcon 1s infinite; /* Pulse animation */
}

/* Pulse Animation for Icons */
@keyframes pulseIcon {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

/* Footer Bottom Styling */
.footer-bottom {
  margin-top: 40px;
  border-top: 2px solid #555;
  padding-top: 20px;
  font-size: 1.2em;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.footer-bottom p {
  margin: 0;
  color: #ccc;
  font-weight: 500;
}

/* Fade-In Animation */
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  .footer-content {
    
    flex-direction: column;
    align-items: center;
  }

  .footer-left, .footer-middle, .footer-right {
    padding: 15px;
    min-width: 100%;
  }

  .footer-left h4, .footer-middle h5, .footer-right h5 {
    font-size: 1.5em;
  }
  .foot{
    margin-top: 335%;
    position: absolute;
    width: 100%;
