    margin-bottom: 20px;
    text-align: center;
  }

  .filter-search-input {
    width: 90%;
    font-size: 14px;
  }

  .filter-dropdown-container {
    display: block;
    text-align: center;
    margin-bottom: 20px;
  }

  .filter-dropdown {
